* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}
.wraper {
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  perspective: 10px;
}
.container {
  position: relative;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transform-style: preserve-3d;
  z-index: -1;
}
.Background {
  transform: translateZ(-40px) scale(5);
}
.foreground {
  transform: translateZ(-20px) scale(3);
}
.Background,
.foreground {
  position: absolute;
  width: 100vw;
  height: 100vh;
  object-fit: cover;
  z-index: -1;
}
h1 {
  position: absolute;
  font-size: 10rem;
  color: white;
  text-shadow: 0 0 10px rgb(0, 0, 0, 0.3);
  top: 3rem;
  left: 50%;
  transform: translateX(-50%);
  letter-spacing: 4px;
}
section {
  background-color: rgb(45, 45, 45);
  color: white;
  padding: 5rem;
}
.secheading {
  font-size: 5rem;
  margin-left: 10rem;
}
.text {
  font-size: 1.5rem;
  padding: 0 10rem;
  margin: 5rem 0;
}
.bg {
  position: relative;
  width: 100%;
  height: 500px;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}
.desc {
  position: absolute;
  background-color: white;
  padding: 0.5rem 2.5rem;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  color: black;
  font-size: 3.5rem;
  font-weight: 600;
}
.bg1 {
  background-image: url("/public/sport-1.jpg");
}
.bg2 {
  background-image: url("/public/sport-2.jpg");
}
.bg3 {
  background-image: url("/public/sport-3.jpg");
}
